import {Button, Form, Input, message} from 'antd';
import React from 'react';
import styled from 'styled-components';
import {RuleObject} from 'rc-field-form/lib/interface';
import {useStores} from '../stores';
import {useNavigate} from 'react-router-dom';

const Wrapper = styled.div`
  max-width: 600px;
  margin: 30px auto;
  padding: 20px;
  box-shadow: 0 0 6px 0 rgba(0, 0, 0, 0.2);
  border-radius: 4px;
`;

const Title = styled.h1`
  text-align: center;
  margin-bottom: 30px;
`;

const Login: React.FC = () => {
  const navigate = useNavigate();
  const {AuthStore} = useStores();
  const onFinish = (values: User) => {
    AuthStore.setUsername(values.username);
    AuthStore.setPassword(values.password);
    AuthStore.login().then(() => {
      navigate('/');
      message.success('', 2).then();
    }).catch(() => {
      message.error('', 2).then();
    });
  };

  const validateUsername = (rule: RuleObject, value: string) => {
    if (/\W/.test(value)) return Promise.reject('');
    if (value.length < 4 || value.length > 10) return Promise.reject('4-10');
    return Promise.resolve().then();
  };

  return (
    <Wrapper>
      <Title></Title>
      <Form
        name="basic"
        labelCol={{span: 6}}
        wrapperCol={{span: 12}}
        initialValues={{remember: true}}
        onFinish={onFinish}
        autoComplete="off"
      >
        <Form.Item
          label=""
          name="username"
          rules={[{required: true, message: ''}, {validator: validateUsername}]}
        >
          <Input/>
        </Form.Item>

        <Form.Item
          label=""
          name="password"
          rules={[{required: true, message: ''}, {min: 4, message: '4'}, {max: 10, message: '10'}]}
        >
          <Input.Password/>
        </Form.Item>

        <Form.Item wrapperCol={{offset: 11, span: 2,}}>
          <Button type="primary" htmlType="submit">
            
          </Button>
        </Form.Item>
      </Form>
    </Wrapper>
  );
};

export default Login;